<template>

	<button
		id="search-button"
		:class="['button-fancy train bm-m end', styleStateCopied]"
		@click="copyToClipboard_OnClick()"
	>
		<img
			width="16"
			height="16"
			src="data:image/png;base64,"
			alt=""
		>
		<span class="lm-m">Copy to Clipboard</span>
	</button>

</template>

<script>
export default {

	data() {

		return {

			copied: false
		}
	},

	methods: {		

		copyToClipboard_OnClick() {

			const text = this.$parent.$refs['result'].innerText;

			navigator.clipboard.writeText(text).then(() => {

				this.copied = true;				

			}, function (err) {

				/* eslint-disable-next-line */
				console.error(err);
			});

		},
	},

	computed: {

		styleStateCopied() {

			return this.copied ? 'button--green' : 'button--blue';
		}
	}

}
</script>

<style scoped>
.button-fancy {
	cursor: pointer;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
		Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
	font-size: 14px;
	font-weight: bold;

	border-radius: 3px;
	padding: 6px 16px;
	color: #fff;
	text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.3);
	box-shadow: 0 1px 0 rgba(255, 255, 255, 0.2) inset;
}

.button--blue {
	background-color: #326ebe;
	border: 1px solid #2e6fb9;
}

.button--green {
	background-color: #5cb85c;
	border: 1px solid #3f853f;
}

.button-fancy:active {
	filter: brightness(90%);
}

.button-fancy:enabled:hover {
	background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1));
}

.end {
	margin-left: auto;
}
</style>